/* Make sure embeds and iframes fit their containers. */
embed,
iframe,
object {
	max-width: 100%;
}

img {
	height: auto; // Make sure images are scaled correctly.
	max-width: 100%; // Adhere to container width.
}

figure {
	margin: 1em 0; // Extra wide images within figure tags don't overflow the content area.
}

.wp-block-soliloquy-soliloquywp {
	display: none;
}

.entry-header, .services-header {
	text-align: center;
	border-bottom: 2px solid black;
	padding-bottom: 16px;
}

.lb-loader,.lightbox{
	text-align:center;
	line-height:0;
	position:absolute;
	left:0
}

body.lb-disable-scrolling{
	overflow:hidden
}

.lightboxOverlay{
	position:absolute;
	top:0;
	left:0;z-index:9999;background-color:#000;
	
filter:alpha(Opacity=80);opacity:.8;display:none}

.lightbox{width:100%;z-index:10000;font-weight:400;outline:0}

.lightbox .lb-image{
	display:block;height:auto;max-width:inherit;max-height:none;border-radius:3px;border:4px solid #fff}
	
.lightbox a img{border:none}

.lb-outerContainer{position:relative;width:250px;height:250px;margin:0 auto;border-radius:4px;background-color:#fff}
.lb-outerContainer:after{content:"";display:table;clear:both}
.lb-loader{top:43%;height:25%;width:100%}.lb-cancel{display:block;width:32px;height:32px;margin:0 auto;background:url(../images/loading.gif) no-repeat}.lb-nav{position:absolute;top:0;left:0;height:100%;width:100%;z-index:10}.lb-container>.nav{left:0}.lb-nav a{outline:0;background-image:url(data:image/gif;base64,R0lGODlhAQABAPAAAP///wAAACH5BAEAAAAALAAAAAABAAEAAAICRAEAOw==)}.lb-next,.lb-prev{height:100%;cursor:pointer;display:block}.lb-nav a.lb-prev{width:34%;left:0;float:left;background:url(../images/prev.png) left 48% no-repeat;filter:alpha(Opacity=0);opacity:0;-webkit-transition:opacity .6s;-moz-transition:opacity .6s;-o-transition:opacity .6s;transition:opacity .6s}.lb-nav a.lb-prev:hover{filter:alpha(Opacity=100);opacity:1}.lb-nav a.lb-next{width:64%;right:0;float:right;background:url(../images/next.png) right 48% no-repeat;filter:alpha(Opacity=0);opacity:0;-webkit-transition:opacity .6s;-moz-transition:opacity .6s;-o-transition:opacity .6s;transition:opacity .6s}.lb-nav a.lb-next:hover{filter:alpha(Opacity=100);opacity:1}.lb-dataContainer{margin:0 auto;padding-top:5px;width:100%;border-bottom-left-radius:4px;border-bottom-right-radius:4px}.lb-dataContainer:after{content:"";display:table;clear:both}.lb-data{padding:0 4px;color:#ccc}.lb-data .lb-details{width:85%;float:left;text-align:left;line-height:1.1em}.lb-data .lb-caption{font-size:13px;font-weight:700;line-height:1em}.lb-data .lb-caption a{color:#4ae}.lb-data .lb-number{display:block;clear:left;padding-bottom:1em;font-size:12px;color:#999}.lb-data .lb-close{display:block;float:right;width:30px;height:30px;background:url(../images/close.png) top right no-repeat;text-align:right;outline:0;filter:alpha(Opacity=70);opacity:.7;-webkit-transition:opacity .2s;-moz-transition:opacity .2s;-o-transition:opacity .2s;transition:opacity .2s}
.lb-data 
.lb-close:hover{cursor:pointer;filter:alpha(Opacity=100);opacity:1}

.img-wrapper {
	display: block;
}

.gallery-link {
	
	margin: 0 auto;
	padding: 0;
	text-align: center;
	display: grid;
	grid-template-columns: repeat(1, 8fr);
	
	@media (min-width: 768px) {
		grid-template-columns: repeat(4, 2fr);
	}


	li {
		list-style: none;
	}

	img {
		width: 80%;
		text-align: center;
		transition: 1s;
	}

	img:hover {
		transform: scale(1.1);
	}
}

.flexslider {
	display: flex;
	margin: 0 auto;
	ul {
		padding: 0;
		margin: 0 auto;
	}

	li {
		list-style: none;
		padding: none;
		margin: 0;
	}

	img {
		width: 100%;
		margin: 0 auto;
		padding: 0;

		@media (min-width: 768px) {
			width: 75%;
		}
	}

	button {
		margin: 0 auto;
		border: none;
		background-color: transparent;
	}

	@media (min-width: 768px) {
		button {
			height: 20%;
			margin-top: 17%;
			font-size: 28px;
		}
	}

	@media (min-width: 1000px) {

		button.w3-button.w3-black.w3-display-left {
			transform: translateX(50%);
		}

		button.w3-button.w3-black.w3-display-right {
			transform: translateX(-50%);
		}

		button {
			margin-top: 20%;
		}
	}

}
